.calculator input[type=button] {

    padding: 6px;
    background-color: chartreuse;

    margin: 1px;

    width: 40px;
    height: 40px;
    text-align: center;
}

.calculator input[type=button]:hover {
    background-color: blueviolet;
    color: white;
}

.calculator {
    background-color: aliceblue;
    padding: 1%;
    margin: auto;
    border: 2px solid black;
    border-radius: 3px;
    width: 200px;
    margin-top: 20%;
    text-align: center;
}
.calculator input[type=text] {
    padding: 6px;
    text-align: right;
}

