.SignUp {
    background-color: chartreuse;
    border: 1px groove blue;
    width: 30%;
    margin: auto;
    padding: 20px;
    margin-top: 15%;
    border-radius: 40px;
}

.SignUp p {
    margin-bottom: 3px;

}

.SignUp input {
    padding: 10px;
    width: 95%;
    border-radius: 10px;
    border: 1px solid white;
}

.SignUp input:hover {
    border: 1px solid blue;
}

.SignUp>div:first-child p {
    /* background-color: red; */
    margin-top: 0px;
}

.SignUp .divTest {
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;

}

.SignUp .divTest input {
    width: 85%;
}

.SignUp .divTest:hover {
    border: 1px solid blue;
}

.SignUp .divTest input:hover,
.SignUp .divTest input:focus-visible,
.SignUp .divTest input:focus {
    border: 1px solid white !important;
    outline: none;
}

.SignUp .divTest i {
    padding: 6px;
}

/*         
        .first {
            margin-top: 0px;
        } */

.SignUp button {
    margin-top: 20px;
    padding: 8px;
    background-color: aqua;
}

.SignUp #buttonon {
    padding-left: 35%;
}

.SignUp body {
    margin: 0px;
}