.card {
    display: inline-block;
    border-radius: 10px;
    padding: 10px;
    
    /* padding-right: 0px; */
    text-align: center;
    background-color: rgb(78, 197, 206);
    border: 1px solid black;
    margin: 10px;
    width: 172px;
    font-size: 25px;
    box-shadow: 10px 10px 20px 0px rgb(31, 93, 97);    
}
.card:hover{
    /* position: relative;
    left: -1%;top: -1%;z-index: 2px; */
    /* padding: ; */
    background-color:  rgb(136, 247, 255);
    transform: scale(1.2);
    
}
  
.container {
    display: flex;
    justify-content: left;
    flex-direction: row;
    flex-wrap: wrap;
    margin: auto;
    /* display: inline-block; */
}
