    margin-top: 2px;
}

.pName {
    
    
    margin-top: 2px;
}
.name {
    float: left;
    top: 0; left: 2px;
}
.id { 
    float: left;
    top: 0; left: 2px;
}
.card div {
text-align: right;

}
.container button {
    float: right;
    position: relative;
    right: 20px;
    border-radius: 10px;
    margin-right: 25px;
    margin-top: 3px;
}


